*{
    margin: 0;
    padding: 0;
}

body, html {
    width: 100%;
    height: 100%;
}

body {
    font-family: "Roboto";
    text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
    background-image: url("./fondo1.png");
}

.container {
	width: 800px;
	height: 700px;
	
	position: absolute;
	top:0;
	bottom: 0;
	left: 0;
	right: 0;
  	
	margin: auto;
}

.container h3 {
    font-style: italic;
    font-weight: 300;
    color: #585858;
}

.randomizer-container {
    display: grid;
    place-content: center;
    width: 800px;
    height: 700px;
    background: #f6f2f2;
    box-shadow: 0px 1px 5px;
}

.title {
    display: grid;
    place-content: center;
    justify-items: center;
}

.title h2 {
    font-family: "Poppins";
    color: #2a2a2a;
    letter-spacing: 5px;
}

.title button {
    margin-top: 20px;
    padding: 15px 40px;
    margin-bottom: 50px;

    font-family: "Roboto";
    border: none;
    cursor: pointer;
    background: #a1a1a1;
    box-shadow: 0px 1px 2px #2a2a2a;
    color: white;
}

.title button:hover {
    background: #757575;
}

.title button:active {
    background-color: #383838;
    box-shadow: 0 1px #666;
    transform: translateY(4px);
}

.grid-container {
    width: 700px;

    align-items: center;
    display: grid;
    grid-template-columns: 110px auto auto;
    padding: 15px;
    background: #c5c5c5;
    box-shadow: 0px 1px 5px;
}

.photo {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    grid-row: 1 / span 3;
}

.name {
    font-weight: 500;
}

.email {
    text-align: right;
}

.age {
    grid-column: 2 / span 2;
}

.country {
    grid-column: 2 / span 2;
}

.lorem-ipsum {
    grid-row-start: 4;
    grid-column: 1 / span 3;

    margin-top: 20px;
    color: #585858;
    text-align: center;
    font-style: italic;
}